		getFollowed: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/follow/listed',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					toUId: uni.getStorageSync('login_key').userId
				},
				success: res => {
					_this.followeds = res.data.data;
				}
			});
		},
		goBack: function() {
